import React, { useState } from "react";
import { ReactSortable } from "react-sortablejs";
import amarilla from "../amarilla.png";
import azul from "../azul.png";
import azulMarino from "../azulMarino.png";
import naranjada from "../naranjada.png";
import rosada from "../rosada.png";
import verde from "../verde.png";

function Paso(props) {
  for (let i = props.actividades.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [props.actividades[i], props.actividades[j]] = [
      props.actividades[j],
      props.actividades[i],
    ];
  }

  const [state, setState] = useState(props.actividades);

  function obtenerImagen(nombre) {
    let imagen;

    switch (nombre) {
      case "azul":
        imagen = azul;
        break;
      case "azulMarino":
        imagen = azulMarino;
        break;
      case "naranjada":
        imagen = naranjada;
        break;
      case "rosada":
        imagen = rosada;
        break;
      case "verde":
        imagen = verde;
        break;
      default:
        imagen = amarilla;
    }

    return imagen;
  }

  return (
    <div className="container text-center contieneCartas">
      <ReactSortable list={state} setList={setState}>
        {state.map((item) => (
          <span className="card" key={item.id}>
            <img
              className="card-img-top"
              src={obtenerImagen(props.color)}
              alt=" "
            />
            <span className="card-body text-justify">
              <div className="container">
                <p className="card-text">{item.titulo}</p>
              </div>
            </span>
          </span>
        ))}
      </ReactSortable>
    </div>
  );
}

export default Paso;
