import React from "react";

function Carrusel() {
  return (
    <div
      id="carouselExampleControls"
      class="carousel slide"
      data-ride="carousel">
      <div class="carousel-inner">
        <div class="carousel-item active">
          <img
            class="d-block w-100 imgCarrusel"
            src="https://cr00.epimg.net/radio/imagenes/2019/05/29/medellin/1559159657_036108_1559160557_noticia_normal.jpg"
            alt="First slide"
          />
        </div>
        <div class="carousel-item imgCarrusel">
          <img
            class="d-block w-100"
            src="https://static.iris.net.co/semana/upload/images/2019/9/20/632724_1.jpg"
            alt="Second slide"
          />
        </div>
        <div class="carousel-item imgCarrusel">
          <img
            class="d-block w-100"
            src="https://estaticos.elperiodico.com/resources/jpg/9/8/buenos-aires-1562985279289.jpg"
            alt="Third slide"
          />
        </div>
      </div>
      <a
        class="carousel-control-prev"
        href="#carouselExampleControls"
        role="button"
        data-slide="prev">
        <span class="carousel-control-prev-icon" aria-hidden="true"></span>
        <span class="sr-only">Anterior</span>
      </a>
      <a
        class="carousel-control-next"
        href="#carouselExampleControls"
        role="button"
        data-slide="next">
        <span class="carousel-control-next-icon" aria-hidden="true"></span>
        <span class="sr-only">Siguiente</span>
      </a>
    </div>
  );
}

export default Carrusel;
